.Home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Home-content {
  display: -ms-flexbox;
  display: flex;
}

.Home-content-photo {
  width: 40%;
  height: 40%;
  border-radius: 20%;
  margin-top: 30px;

  /* Aplica una rotación a la imagen */
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.Home-content-text {
  width: 50%;
  margin-left: 50px;
  font-size: 1.5em;
}

.Home-content-text-paragraph {
  font-size: 1.5em;
}

.Home-content-text-title-span {
  color: #42c5f4;
}


